<template src="./MusicPlayer.html"></template>
<style src="./MusicPlayer.css"></style>
<script>
export default {
    name: 'MusicPlayer',
    data() {
        return {
            currentTrack: null,
            audioDurations: {} // –û–±—ä–µ–∫—Ç –¥–ª—è "–¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–µ–π"
        }
    },
    props: {
        tracks: { type: Array, default: () => [] }
    },
    computed: {
        preparedTracks() {
            if (!this.tracks || !Array.isArray(this.tracks)) {
                return [];
            }
            
            let resArr = this.tracks.map((track, index) => {
                if (!track || typeof track !== 'object') {
                    console.warn('Invalid track object::', index);
                    return null;
                }
                
                const trackId = this.getStableTrackId(track, index);
                const trackKey = `${track.name}-${track.size}`;
                let finalObj = {
                    id: trackId,
                    name: track.name || 'Unknown',
                    common: track.common || {},
                    duration: this.audioDurations[trackKey] || null,
                    url: URL.createObjectURL(track),
                    key: trackKey
                };

                return finalObj;
            }).filter(track => track !== null);

            return resArr;
        },
    },
    watch: {
        tracks: {
            handler(newTracks) {
                if (newTracks && newTracks.length > 0) this.preloadDurationsSimple(newTracks);
            },
            immediate: true //–¥–µ–ª–∞—Ç—å —Å—Ä–∞–∑—É, –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –Ω–∞–±–ª—é–¥–∞—Ç–µ–ª—è (–±–µ–∑ –Ω–µ–≥–æ - –±—É–¥–µ—Ç –∂–¥–∞—Ç—å –ø–æ—Å–ª–µ–¥. –∏–∑–º–µ–Ω–µ–Ω–∏—è)
        }
    },
    methods: {
        getStableTrackId(track, index) {
            if (track.id) return track.id;
            
            // –ü—Ä–æ—Å—Ç–∞—è –≥–µ–Ω–µ—Ä–∞—Ü–∏—è ID
            return `track-${index}-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
        },
        async preloadDurationsSimple(tracks) {
            for (const track of tracks) {
                const trackKey = `${track.name}-${track.size}`;
                
                if (!this.audioDurations[trackKey]) {
                    try {
                        this.audioDurations[trackKey] = await this.getAudioDurationSimple(track);
                    } catch (error) {
                        console.warn(`–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å –¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å –¥–ª—è ${track.name}:`, error);
                        this.audioDurations[trackKey] = 0;
                    }
                }
            }
        },
        // –≤—ã—á–∏—Å–ª–µ–Ω–∏–µ –¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ —Ñ–∞–π–ª–∞
        getAudioDurationSimple(track) {
            return new Promise((resolve, reject) => {
                const audio = new Audio();
                const objectURL = URL.createObjectURL(track);
                
                audio.src = objectURL;
                
                const mkClean = () => {
                    audio.removeEventListener('loadedmetadata', onLoaded);
                    audio.removeEventListener('error', onError);
                    URL.revokeObjectURL(objectURL); // –û—Å–≤–æ–±–æ–∂–¥–∞–µ–º —Å—Å—ã–ª–∫—É –≤ –ø–∞–º—è—Ç–∏ "URL" - —Å—Ä–∞–∑—É
                };
                
                // —á–∏—Å—Ç–∏–º —Å—Å—ã–ª–∫–∏, –¥–æ—Å—Ç–∞–µ–º –¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å
                const onLoaded = () => {
                    mkClean();
                    resolve(audio.duration);
                };
                // —á–∏—Å—Ç–∏–º —Å—Å—ã–ª–∫–∏, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –æ—à–∏–±–∫—É
                const onError = (e) => {
                    mkClean();
                    reject(e);
                };
                
                audio.addEventListener('loadedmetadata', onLoaded); //–ø–æ–¥–ø–∏—Å–∫–∞ –Ω–∞ —Å–æ–±—ã—Ç–∏–µ "–∑–∞–≥—Ä—É–∑–∫–∞ –º–µ—Ç–∞-–¥–∞–Ω–Ω—ã—Ö"
                audio.addEventListener('error', onError); //–ø–æ–¥–ø–∏—Å–∫–∞ –Ω–∞ —Å–æ–±—ã—Ç–∏–µ "–æ—à–∏–±–∫–∞"
                
                // –¢–∞–π–º–∞—É—Ç
                setTimeout(() => {
                    mkClean();
                    reject(new Error('Timeout loading audio metadata'));
                }, 3000);
            });
        },
        formatDuration(seconds) {
            if (!seconds || isNaN(seconds)) {
                return '00:00';
            } else {
                return `${Math.floor(seconds / 60).toString().padStart(2, '0')}:${Math.floor(seconds % 60).toString().padStart(2, '0')}`;
            }
        },
        SelectedTrack(track) {
            if (!this.currentTrack || this.currentTrack.id !== track.id) {
                this.currentTrack = track;
                this.$emit("selected-track", track);
            }
        }
    }
};
</script>